.container {
  background-color: grey;
  width: 1000px;
  height: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  //grid-column-gap: 30px
  //grid-row-gap: 30px
  grid-gap: 30px; }

.item {
  padding: 20px;
  font-size: 30px;
  font-family: sans-serif;
  color: white;
  &-1 {
    background-color: red;
    //grid-row-start: 2
    //grid-row-end: 3
    //grid-column-start: 2
    //grid-column-end: 3
    //grid-row: 2 / 3
    //grid-column: 2 / 3
    grid-area: 2 / 2 / 3 /3; }
  &-2 {
    background-color: pink; }

  &-3 {
    background-color: blue; }
  &-4 {
    background-color: brown; }
  &-5 {
    background-color: orange; }
  &-6 {
    background-color: green;
    grid-column: 2 / 3;
    grid-row: 1 / 2; } }





